<template>
	<view class="content">
		<view class="search-form-inner">
			<view class="search-box">
				<view class="search-icon"></view>
				<input :focus="true" @confirm="onSearchConfirm" class="input"
					confirmType="search" maxlength="maxLen" name="keyword" :placeholder="preKeyword||'搜素你感兴趣的'"
					placeholderClass="placeholder" type="text" :value="keyword"></input>
				<view @click="clean" class="delete-icon" v-if="keyword.length>0"></view>
			</view>
			<button class="search-btn" @tap="onSearchConfirm" v-if="keyword">搜索</button>
			<button @tap="onCancelBtnClick" class="search-btn" v-else>取消</button>
		</view>
		<view class="hit_search" v-if="searchViewVisible">
			<u-section font-size="36" :show-line="false" title="热门搜索" :right="false"></u-section>
			<view class="hit_words">
				<view @tap="searchStat(item.name)" class="relative" v-for="(item,index) in tags" :key="index">
					<view class="keywords">{{item.name}}</view>
				</view>
			</view>
		</view>
		<view class="resulut" v-if="!searchViewVisible">
			<article-list :posts="posts"></article-list>
			<u-loadmore :status="status" />
		</view>
	</view>
</template>

<script>
	import {Request} from '@/api/request.js'
	export default {
		data() {
			return {
				keyword: '',
				tags: [{
						name: "习近平"
					},
					{
						name: "非正式"
					}
				],
				posts: [{
						detailId: "1",
						title: "中办印发通知：在全社会开展“四史”宣传教育中办印发通知：在全社会开展“四史”宣传教育",
						comments: "10",
						views: "10",
						thumbnail: "https://img.3dmgame.com/uploads/images/news/20210817/1629191900_447274.jpg",
						time: "2020-11-11",
						likes: 10,
					},
					{
						detailId: "1",
						title: "中办印发通知：在全社会开展“四史”宣传教育中办印发通知：在全社会开展“四史”宣传教育",
						comments: "10",
						views: "10",
						thumbnail: "https://img.3dmgame.com/uploads/images/news/20210817/1629191900_447274.jpg",
						time: "2020-11-11",
						likes: 10,
					},
					{
						detailId: "1",
						title: "中办印发通知：在全社会开展“四史”宣传教育中办印发通知：在全社会开展“四史”宣传教育",
						comments: "10",
						views: "10",
						thumbnail: "https://img.3dmgame.com/uploads/images/news/20210817/1629191900_447274.jpg",
						time: "2020-11-11",
						likes: 10,
					},
					{
						detailId: "1",
						title: "中办印发通知：在全社会开展“四史”宣传教育中办印发通知：在全社会开展“四史”宣传教育",
						comments: "10",
						views: "10",
						thumbnail: "https://img.3dmgame.com/uploads/images/news/20210817/1629191900_447274.jpg",
						time: "2020-11-11",
						likes: 10,
					}
				],
				page: 1,
				preKeyword: '搜索你感兴趣的内容',
				searchViewVisible: true,
			}
		},
		onLoad() {
			this.getTags();
		},
		onReachBottom() {
			
		},
		methods: {
			getTags: function() {
				Request({
					url:"tags/getTags",
					method:"get",
				}).then(res => {
					this.tags=res.data;
					console.log(res);
				})
				.catch(cat => {
					console.log(cat);
				});
			},
			searchStat: function(keyword) {
				this.keyword = keyword;
				this.onSearchConfirm();
			},
			onSearchConfirm: function() {
				this.posts = [];
				this.status = "loading";
				this.searchViewVisible = false;
				this.selectPostsByLike();
			},
			clean: function() {
				this.keyword = '';
				this.posts = [];
				this.status = "loading";
				this.searchViewVisible = true;
			},
			onCancelBtnClick: function() {
				uni.navigateBack({
					delta: 1
				})
			},
			// 获取post信息
			async selectPostsByLike(id){
				await Request({
					url:"detail/selectPostsByLike",
					method:"post",
					data:this.keyword
				}).then(res => {
					this.status = "";
					this.posts=res.data;
					console.log(res);
				})
				.catch(cat => {
					console.log(cat);
				});
			},
		},
	}
</script>

<style lang="scss" scoped>
	.search-form-inner {
		position: fixed;
		left: 0;
		padding: 30rpx;
		height: 100rpx;
		display: flex;
		justify-content: space-between;
		align-items: center;
		box-sizing: border-box;
		background-color: #fff;
		z-index: 11;

		.search-box {
			flex: 1;
			width: 550rpx;
			height: 60rpx;
			padding: 0 14rpx 0 30rpx;
			border-radius: 30rpx;
			display: flex;
			align-items: center;
			background-color: #f4f3f7;

			.search-icon {
				width: 28rpx;
				height: 28rpx;
				background: url('data:image/png;base64,') no-repeat 0 0;
				background-size: contain;
				margin-right: 18rpx;
			}

			.delete-icon {
				width: 38rpx;
				height: 38rpx;
				background: url('data:image/png;base64,') no-repeat 0 0;
				background-size: contain;
				margin-right: 18rpx;
				z-index: 999;
			}


			.input {
				flex: 1;
				font-size: 28rpx;
				color: #252628;
			}

			.placeholder {
				font-size: 28rpx;
				color: #b7b5c2;
			}
		}

		.search-btn,
		.cancel-btn {
			width: 120rpx;
			margin-left: 30rpx;
			padding: 0;
			border: none;
			font-size: 28rpx;
			background: #fff;
			color: #434343;
		}

	}

	.hit_search {
		padding: 133rpx 32rpx 0;

		.hit_words,
		.relative {
			display: flex;
			flex-wrap: wrap;
			justify-content: flex-start;
		}

		.keywords {
			color: #6b6a72;
			display: inline-block;
			background: #f4f3f7;
			border-radius: 100rpx;
			padding: 14rpx 33rpx;
			margin: 20rpx 20rpx 0 0;
			max-width: 260rpx;
			overflow: hidden;
			text-overflow: ellipsis;
			white-space: nowrap;
			font-size: 22rpx;
		}
	}

	.resulut {
		padding: 100rpx 0rpx;
	}
</style>
